.container{
    width : 100% ; 
    height : 100% ; 
    background-color: cyan;
    display : flex ; 
    align-items: center ;
    justify-content: space-between;
    flex-direction: column ;
    position: relative ; 
}

.board-container{
    width: 100% ; 
    height : 70% ; 
    background-color: white;
    display : flex ; 
    align-items: center ;
    justify-content: space-evenly;
    flex-direction: column ;
}

.status {
    width : 100% ; 
    height : 30% ;  
    background-color: yellow;
    display : flex ; 
    align-items: center;
    justify-content: center ;
    flex-direction: column ;
    font-size : 3rem ; 
}
.row {
    width : 70% ; 
    height : 25% ; 
    background-color: yellow ;
    display: flex ;
    align-items: center;
    justify-content: space-evenly;
}